.footer_outer_container{
    background-color: #111;
    color: gray;
    display: flex;
    justify-content: center;
}
.footer_inner_container{
  width: 60%;
}
.footer_icons{
    display: flex;
    justify-content: space-between;
    width: 12%;
    margin-left: 10px;
    color: #fff;
    /* gap: 20px; */
}
.footer_data{
    display: flex;
    justify-content: space-between;
}
.footer_data ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 12vh;
}
.footer_data ul li{
    cursor: pointer;
}

.service_code{
    display: flex;
    justify-content: center;
    align-items: center;
    border: #fff solid 1px;
    width: 10%;
    margin: 10px 0;
    cursor: pointer;
}
.copy_write{
    font-size: 12px;
    margin-bottom: 10px;
    left: 0;
}