
.banner{
    margin-top: 0vh;
    height: 80vh;
    margin-bottom: -80px;
    color: white;
    object-fit: contain;

}
.banner_contents{
    margin-left: 30px;
    padding-top: 30px;
}
.banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner_description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1.2rem;
    height: 80px;
    max-width: 360px;
}
.banner_button{
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-size: 1.2rem;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 1rem 3rem;
    margin-right: 1rem;
    background-color: rgba(51,51,51,0.5);
}
.play{
    color: #000;
    background-color: #fff;
}
.banner_button:hover{
    color: #000;
    background-color: #c6c6c6;
    transition: all 0.2s;
}
.banner_fadeButton{
    height: 40vh;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37,0.91),
        #111
    );
}